@import (reference) url('../../../node_modules/xmeter/css/src/__tool.delims.less');


.c-MemberStory {
  @supports (display: grid) { // NB-LINK read .o-Grid--.less#L17 for details
    display: grid;
  }
  grid-template: max-content auto auto ~'/' repeat(2, 1fr);
}

.c-MemberStory__Img {
  // .h-Block
    display: block;
    line-height: inherit; // COMBAK-FALLBACK
    line-height: unset;
  grid-row: 1 ~'/' -2;
  margin: auto;
  margin-bottom: 1.5rem;    // TODO update Bangs and then add class `.-mb-1`
  margin-bottom: var(--lh); // TODO update Bangs and then add class `.-mb-1`
  margin-bottom: 1lh;       // TODO update Bangs and then add class `.-mb-1`
  border-radius: 50%;
  box-shadow: 0 0 0 0.25rem var(--color-accent, transparent); // Box-Shadow hack: doesn’t fuck with vertical rhythm: read https://github.com/chharvey/xmeter/blob/v6.2.0/docs/tools.md#box-shadow-hack
  // border: 0.25rem solid var(--color-accent, transparent);
}

.c-MemberStory__Hn,
.c-MemberStory__SubHn {
  text-align: center;
}

.c-MemberStory__Hn {
  font-weight: 600;
}

.c-MemberStory__SubHn {
}

.c-MemberStory__Quote {
  grid-column: 1 ~'/' -1;
  padding-left: 0.5rem;
  border-left: 0.25rem solid var(--color-accent, transparent);
  .quotes-double();
  font-style: italic;
}

@media screen and (min-width: 30em) and (max-width: 59.999em) {
  .c-MemberStory {
    grid-template: max-content auto ~'/' 1fr 2fr 3fr;
    grid-column-gap: var(--layout-padding-inline);
  }
  .c-MemberStory__Img,
  .c-MemberStory__Quote {
    grid-row: 1 ~'/' -1;
    align-self: start;
  }
  .c-MemberStory__Quote {
    grid-column: 3;
  }
}
