/*################################*\
    anakin  |  _base.less
\*################################*/
html {
  --layout-padding-base: 0.25;
  --layout-padding-block: calc(var(--layout-padding-base) * var(--lh));
  --layout-padding-inline: calc(var(--layout-padding-base) * 2rem);
  --layout-padding: var(--layout-padding-block) var(--layout-padding-inline);
}
@media screen and (min-width: 30em) {
  html {
    --layout-padding-base: 0.5;
  }
}
@media screen and (min-width: 45em) {
  html {
    --layout-padding-base: 1;
  }
}
@media screen and (min-width: 75em) {
  html {
    --layout-padding-base: 2;
  }
}
body {
  font-family: 'Open Sans', sans-serif;
  color: #333;
}
li:nth-of-type(1) {
  --color-accent: #ffda54;
}
li:nth-of-type(2) {
  --color-accent: #00c0ca;
}
li:nth-of-type(3) {
  --color-accent: #ff9400;
}
table {
  width: 100%;
  font-size: 0.75rem;
}
table > * {
  --lh: calc(0.75  * var(--line-height) * 1rem);
}
thead,
tfoot {
  background-color: #f4f4f4;
  font-weight: 600;
  font-size: 1.16666667em;
  line-height: calc(var(--line-height) /  1.16666667);
}
th,
td {
  padding: calc(0.5 * var(--vru)) 0.5rem;
  border: 0 solid #e8e8e8;
  border-width: 0 0 1px;
}
a[href] {
  color: #0c5fa8;
  transition: 250ms;
  transition-property: color, background-color;
}
a[href]:hover,
a[href]:focus {
  color: #19a1cf;
}
/*################################*\
    anakin  |  _o-Grid--.less
\*################################*/
.o-Grid {
  display: block;
}
@supports (display: grid) {
  .o-Grid {
    display: grid;
  }
}
@media screen and (min-width: 45em) {
  .o-Grid--jobs,
  .o-Grid--publications,
  .o-Grid--homeActions,
  .o-Grid--news {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 45em) {
  .o-Grid--jobs,
  .o-Grid--news {
    grid-template-rows: repeat(2, auto);
  }
}
@media screen and (min-width: 30em) {
  .o-Grid--represent {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 60em) {
  .o-Grid--members {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 30em) {
  .o-Grid--portals {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 45em) {
  .o-Grid--portals {
    grid-template-columns: repeat(3, 1fr);
  }
}
.o-Grid--represent,
.o-Grid--portals,
.o-Grid--publications,
.o-Grid--homeActions,
.o-Grid--jobs,
.o-Grid--news,
.o-Grid--members {
  grid-row-gap: var(--layout-padding-block);
  grid-column-gap: var(--layout-padding-inline);
}
@supports (display: grid) {
  @media screen and (min-width: 45em) {
    .o-Grid--publications > li:first-of-type,
    .o-Grid--news > li:first-of-type {
      position: relative;
    }
    .o-Grid--publications > li:first-of-type::after,
    .o-Grid--news > li:first-of-type::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: calc(-0.5 * var(--layout-padding-inline));
      width: calc(0.125 * var(--lh));
      inline-size: 0.125lh;
      background: linear-gradient(to bottom, transparent, #e8e8e8, transparent);
    }
  }
}
.o-Grid--news > li:first-of-type {
  grid-row: 1 / -1;
}
.o-Grid--foundation {
  --color-accent: #19a1cf;
  grid-template-areas: 'heading heading' 'caption caption' 'icon    blurb  ' 'icon    button ';
  grid-column-gap: var(--layout-padding-inline);
}
@media screen and (min-width: 30em) {
  .o-Grid--foundation {
    grid-template: 'heading icon blurb ' 'caption icon button' / 2fr 1fr 1fr;
  }
}
.o-Grid--foundation > h1 {
  grid-area: heading;
}
.o-Grid--foundation > p.o-Grid--foundation__Caption {
  grid-area: caption;
}
.o-Grid--foundation > i {
  grid-area: icon;
}
.o-Grid--foundation > p.o-Grid--foundation__Blurb {
  grid-area: blurb;
}
.o-Grid--foundation > p.o-Grid--foundation__Action {
  grid-area: button;
}
.o-Grid--foundation > .o-Grid--foundation__Blurb,
.o-Grid--foundation > .o-Grid--foundation__Action {
  text-align: center;
}
/*################################*\
    anakin  |  _o-Tablist--slider.less
\*################################*/
.o-Tablist--slider {
  position: relative;
  justify-content: center;
  margin-bottom: var(--lh);
}
.o-Tablist--slider .o-Tablist__Tab {
  flex: none;
  order: 1;
  padding: 0 2ch;
  text-align: center;
}
.o-Tablist--slider .o-Tablist__Tab[aria-selected="true"] {
  font-weight: normal;
}
.o-Tablist--slider .o-Tablist__Tab[aria-selected="false"] {
  opacity: 0.5;
}
.o-Tablist--slider .o-Tablist__Panel {
  order: 0;
}
.o-Tablist--slider .o-Tablist--slider__Button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  margin: 0 6.25%;
  font-size: 2em;
  background: none;
  border: 0;
  color: white;
  text-shadow: 0 0 0.125rem rgba(0, 0, 0, 0.5);
  text-shadow: 0 0 0.125rem #00000080;
}
.o-Tablist--slider .o-Tablist--slider__Button[value="prev"] {
  left: 0;
}
.o-Tablist--slider .o-Tablist--slider__Button[value="next"] {
  right: 0;
}
.o-Tablist--slider .o-Tablist__Tab {
  position: relative;
  top: calc(-2 * var(--lh));
  z-index: 1;
  color: white;
}
.c-Promo__Bar {
  padding-bottom: calc(2 * var(--lh)) !important;
  padding-bottom: 2lh !important;
}
/*################################*\
    anakin  |  _o-Runner.less
\*################################*/
.o-Runner {
  padding: var(--layout-padding);
}
.o-Runner--separator {
  position: relative;
}
.o-Runner--separator::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: calc(0.125 * var(--lh));
  block-size: 0.125lh;
  background: linear-gradient(to right, transparent, #e8e8e8, transparent);
}
/*################################*\
    anakin  |  _c-SectionHed.less
\*################################*/
.c-SectionHed {
