/*################################*\
    anakin  |  _c-Button.less
\*################################*/
.c-Button {
  font-family: 'futura-pt', 'Open Sans', 'Helvetica Neue', 'Helvetica', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  word-spacing: 1px;
  letter-spacing: 1px;
  display: inline-block;
  padding: calc(0.5 * var(--vru)) 1rem;
  font-size: 0.875em;
  white-space: nowrap;
  vertical-align: middle;
  background-color: #0c5fa8;
  border-radius: 0.25rem;
  box-shadow: 0 0.125em 0 0 #cdcdcd;
  cursor: pointer;
}
.c-Button,
a[href].c-Button,
.c-Button:hover,
a[href].c-Button:hover,
.c-Button:focus,
a[href].c-Button:focus {
