}
.c-Button:hover,
.c-Button:focus {
  background-color: #0e6cc0;
}
.c-Button:active {
  background-color: #0a5290;
  box-shadow: 0 0.125em 0 0 #cdcdcd inset;
}
.c-Button--ghost {
  background: transparent;
  box-shadow: 0 0 0 0.125rem white;
  text-shadow: none;
}
